import Vue from 'vue'
import VueRouter from 'vue-router'

import Index from '@/components/Index.vue'
import ListRecord from '@/components/record/ListRecord.vue'
import Worth from '@/components/common/WorthDialog.vue'


Vue.use(VueRouter)

const routes = [
    {
        path: "/",
        name: 'Index',
        component: Index
    },
    {
        path: "/record/list",
        name: 'ListRecord',
        component: ListRecord
    },
    {
        path: "/worth",
        name: 'Worth',
        component: Worth
    }
]

const router = new VueRouter({
    mode: 'hash',
    routes
})

export default router
