<template>
  <div>
    <Lead title="补仓计算" content=""/>
    <div style="width: 1200px;margin-left: auto;margin-right: auto;margin-top: 15px">

      <el-card>
        <el-form ref="form" :rules="rules" :model="form" label-width="120px">
          <el-form-item label="基金代码" prop="code">
            <el-input  v-model="form.code">
              <el-button slot="append" @click="selectCode" icon="el-icon-search"></el-button>
            </el-input>
          </el-form-item>

          <el-form-item v-if="oneInfo.name" label="基金名称">
            <el-input v-model="oneInfo.name"></el-input>
          </el-form-item>

          <el-form-item v-if="oneInfo.dwjz" label="单位净值">
            <el-input v-model="oneInfo.dwjz"></el-input>
          </el-form-item>

          <el-form-item label="持仓金额" prop="oldMoney">
            <el-input v-model="form.oldMoney"></el-input>
          </el-form-item>

          <el-form-item label="持仓价格" prop="oldPrice">
            <el-input v-model="form.oldPrice"></el-input>
          </el-form-item>

          <el-form-item label="持仓份额" prop="oldPart">
            <el-input v-model="form.oldPart"></el-input>
          </el-form-item>

          <el-form-item label="收益率(%)" prop="lossPercent">
            <el-input v-model="form.lossPercent"></el-input>
          </el-form-item>

          <el-form-item label="补仓金额" prop="addMoney">
            <el-input v-model="form.addMoney"></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="onSubmit">补仓计算</el-button>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="fetchSaveBuyRecord">保存计算</el-button>
          </el-form-item>

        </el-form>

      </el-card>


      <div>
        <el-table
            :row-class-name="tableRowClassName"
            style="margin-top: 15px;margin-bottom: 65px"
            :data="tableData">
          <el-table-column
              prop="money"
              label="补仓金额">
          </el-table-column>

          <el-table-column
              prop="percent"
              label="亏损百分比(%)">
          </el-table-column>

        </el-table>

        <!--    <el-pagination
                style="margin-top: 15px"
                background
                layout="prev, pager, next"
                :total="100">
            </el-pagination>-->
      </div>


    </div>


    <Footer/>
  </div>
</template>

<script>
import Lead from "./Lead";
import Footer from "./Footer";
import {requestAddMoney, requestCodeInfo, requestSaveBuyRecord} from "@/api";


export default {
  name: "Index",
  components: {Footer, Lead},
  created() {
  },
  methods: {
    selectCode() {
      // len 6
      if (this.form.code.length==6){
        this.fetchCodeInfo()
      }
    },

    async fetchSaveBuyRecord() {
      const response = await requestSaveBuyRecord({
        code: this.form.code,
        name: this.oneInfo.name,
        price: this.oneInfo.dwjz,

        addMoney: this.form.addMoney,
        oldPrice: this.form.oldPrice,
        oldPart: this.form.oldPart,
        oldMoney: this.form.oldMoney,
        lossPercent: this.form.lossPercent,
      })
      if (response && response.code == 100000) {
        this.$message({
          message: '保存成功',
          type: 'success'
        })
      } else {
        this.$message({
          showClose: true,
          message: response.message,
          type: 'error'
        })
      }
    },

    async fetchCodeInfo() {
      const response = await requestCodeInfo({
        code: this.form.code,
      })
      if (response && response.code == 100000) {
        this.$message({
          message: '获取成功',
          type: 'success'
        })
        this.oneInfo = response.data.oneInfo
      } else {
        this.$message({
          showClose: true,
          message: response.message,
          type: 'error'
        })
        this.oneInfo = {}
      }
    },
    onSubmit() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          this.fetchAddMoney()
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    async fetchAddMoney() {
      const response = await requestAddMoney({
        addMoney: this.form.addMoney,
        oldPrice: this.form.oldPrice,
        oldPart: this.form.oldPart,
        oldMoney: this.form.oldMoney,
        lossPercent: this.form.lossPercent,
      })
      if (response && response.code == 100000) {
        this.$message({
          message: '计算成功',
          type: 'success'
        })
        this.tableData = response.data.buyPercentList
        // this.$refs['form'].resetFields();
      } else {
        this.$message({
          showClose: true,
          message: response.message,
          type: 'error'
        })
      }
    },
    tableRowClassName({row, rowIndex}) {
      if (rowIndex === 1) {
        return 'warning-row';
      } else if (rowIndex === 3) {
        return 'success-row';
      }
      return '';
    }
  },

  data() {
    return {
      rules: {
        addMoney: [
          {required: true, message: '补仓金额', trigger: 'blur'},
        ],
        oldPrice: [
          {required: true, message: '持仓价格', trigger: 'blur'},
        ],
        oldPart: [
          {required: true, message: '持仓份额', trigger: 'blur'},
        ],
        oldMoney: [
          {required: true, message: '持仓金额', trigger: 'blur'},
        ],
        lossPercent: [
          {required: true, message: '收益率(%)', trigger: 'blur'},
        ],
      },
      oneInfo: {},
      form: {
        code: "005562",
        addMoney: '',
        oldPrice: '',
        oldPart: '',
        oldMoney: '',
        lossPercent: '',
      },
      tableData: [],
    }
  }
}
</script>

<style>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
</style>