import Axios from 'axios'

// 5分钟
Axios.defaults.timeout = 1000*60*5;

export default function ajax(url, data = {}, type = "GET") {
    return new Promise((resolve, reject) => {
            console.log("请求参数")
            console.log(data)
            let promise
            if (type === "GET") {
                promise = Axios.get(url, {
                    /* headers: {'Content-Type': 'application/x-www-form-urlencoded'},*/
                    params: data
                })
            } else if (type == "POST") {
                const config = {
                    headers: {'Content-Type': 'application/x-www-form-urlencoded'},
                    params: data
                }
                promise = Axios.post(url, data, config)
            } else if (type == "BODY") {
                promise = Axios.post(url, data)
            }

            promise.then(response => {
                console.log(response.data)
                resolve(response.data)
            })

            promise.catch(error => {
                if (error.response) {
                    //debugger
                    // 响应码
                    const status = error.response.status;
                    // 响应消息体
                    const responseText = error.response.data.message;
                    let message = "";
                    if (status == 500) {
                        message = "服务器发生异常";
                    } else if (status == 401) {
                        message = "会话过期请重新登录";
                    } else if (status == 404) {
                        message = "请求地址不存在";
                    } else if (status == 400) {
                        message = "请求参数不正确";
                    } else if (status == 403) {
                        message = "请求资源未授权";
                    } else {
                        message = "未知错误";
                    }
                    //alert(message)
                    //alert(error.request)
                    console.log(error.request)
                    this.$message({
                        showClose: true,
                        message: message,
                        type: 'warning'
                    })
                    /*Toast({
                        position: 'bottom',
                        duration: 2000,
                        message: message
                    })*/
                } else {
                    this.$message({
                        showClose: true,
                        message: "网络请求异常",
                        type: 'warning'
                    })
                    /*Toast({
                        position: 'bottom',
                        duration: 2000,
                        message: "网络请求异常"
                    })*/
                }


            })
        }
    )

}
