<template>
  <div style="width: 1200px;margin-left: auto;margin-right: auto;margin-top: 15px">
    <el-table
        style="margin-top: 15px;margin-bottom: 65px"
        :data="tableData">
      <el-table-column
          prop="code"
          label="基金代码">
      </el-table-column>

        <el-table-column
            prop="name"
            label="基金名称">
        </el-table-column>

      <el-table-column
          prop="price"
          label="单位净值">
      </el-table-column>

      <el-table-column
          prop="oldMoney"
          label="持仓金额">
      </el-table-column>

      <el-table-column
          prop="oldPrice"
          label="持仓价格">
      </el-table-column>

      <el-table-column
          prop="oldPart"
          label="持仓份额">
      </el-table-column>

      <el-table-column
          prop="lossPercent"
          label="收益率(%)">
      </el-table-column>

      <el-table-column
          prop="addMoney"
          label="补仓金额">
      </el-table-column>

      <el-table-column label="操作" width="250">
        <template slot-scope="scope">


          <el-button
              type="primary"
              size="mini"
              @click="handleDetail(scope.$index, scope.row)"
          >补仓率
          </el-button>

          <el-button
              type="primary"
              size="mini"
              @click="handleWorth(scope.$index, scope.row)"
          >业绩走势
          </el-button>

          <el-button
              type="danger"
              size="mini"
              @click="handleDelete(scope.$index, scope.row)"
          >删除
          </el-button>
        </template>
      </el-table-column>

    </el-table>

    <el-pagination
        @size-change="handleSizeChange"
        @current-change="currentPageChangeEvent"
        :current-page="currentPage"
        :page-sizes="[5, 10, 20, 50]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalElements"
        :page-count="totalPages"
        background
    >
    </el-pagination>


    <el-dialog
        :close-on-click-modal="false"
        title="补仓率表格数据"
        :visible.sync="percentShow"
        width="50%">
      <!--          style="margin-top: 15px;margin-bottom: 65px"-->
      <el-table
          :data="percentList">
        <el-table-column
            prop="money"
            label="补仓金额">
        </el-table-column>

        <el-table-column
            prop="percent"
            label="当前亏损(%)">
        </el-table-column>
      </el-table>

    </el-dialog>

    <el-dialog
        title="业绩走势"
        :close-on-click-modal="false"
        :visible.sync="worthShow"
        :fullscreen="false"
        width="80%">
      <WorthDialog :code="code" :key="uuid"></WorthDialog>
    </el-dialog>

  </div>
</template>

<script>

import {requestBuyPercentList, requestBuyRecordList, requestDeleteBuyRecord} from "@/api";
import Worth from "@/components/common/WorthDialog.vue";
import {uuid} from "@/util";
import WorthDialog from "@/components/common/WorthDialog.vue";

export default {
  name: "ListRecord",
  components: {WorthDialog, Worth},
  methods: {


    handleSizeChange(size) {
      this.currentPage = 1;
      this.pageSize = size;
      this.fetchBuyRecordList()
    },
    // 页码改变
    currentPageChangeEvent(currentPage) {
      this.currentPage = currentPage;
      this.fetchBuyRecordList()
    },

    handleWorth(index,row){
      this.code = row.code
      this.uuid = uuid()
      this.worthShow = true
    },

    async handleDetail(index, row) {
      this.percentShow = true
      const response = await requestBuyPercentList({
        buyRecordId:row.buyRecordId
      })

      if (response && response.code == 100000) {
        this.percentList = response.data.list

      }else {
        this.$message({
          showClose: true,
          message: response.message,
          type: 'error'
        })
      }

    },
    async handleDelete(index, row) {
      this.$confirm('此操作将永久删除该补仓率, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const response = await requestDeleteBuyRecord({
          buyRecordId: row.buyRecordId
        })
        if (response && response.code == 100000) {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
          await this.fetchBuyRecordList()
        } else {
          this.$message({
            showClose: true,
            message: response.message,
            type: 'error'
          })
        }
      })


    },
    async fetchBuyRecordList() {
      const response = await requestBuyRecordList({
        pageNum:this.currentPage,
        pageSize:this.pageSize
      })
      if (response && response.code == 100000) {
        this.tableData = response.data.list

        this.totalElements =response.data.totalElements
        this.totalPages = response.data.totalPages
      } else {
        this.$message({
          showClose: true,
          message: response.message,
          type: 'error'
        })
      }
    }
  },
  mounted() {
    this.fetchBuyRecordList()
  },
  data() {
    return {
      tableData: [],

      percentList:[],

      percentShow: false,
      worthShow: false,

      currentPage: 1,
      totalElements: 0,
      pageSize: 5,
      totalPages: 0,
      code:"",
      uuid:""

    }
  }
}
</script>

