<template>
<!--  <div style="width: 1200px;margin-left: auto;margin-right: auto;margin-top: 15px">-->
  <div style="">
    <div>{{code}}</div>
    <div id="worth" ref="worthChart" style="width: 600px;height: 650px"></div>
    <div>
      开始时间 {{startDate}}<br>
      结束时间 {{endDate}}<br>
      区间涨跌 {{addUp}}<br>
    </div>
  </div>

</template>

<script>
import {requestWorthList} from "@/api";

let worthChart;
export default {

  props: {code: ""},
  data() {
    return {
    // code: "",
      startDate: "",
      endDate: "",
      addUp:0,
    }
  },
  mounted() {
    this.initWorthChart()
  },
  created() {
    this.loadData()
  },
  methods: {
    loadData() {
      this.fetchWorthData()
    },
    async fetchWorthData() {
      const response = await requestWorthList({
        code: this.code
      })
      if (response && response.code == 100000) {
        const list = response.data.list

        let valueArr = []
        let categoryArr = []
        let volumes = []
        list.forEach((item, index) => {
          valueArr.push([item.date, item.y])
          categoryArr.push(item.date)
          volumes.push(item.y)
        })

        let option = {
          series: [
            {data: valueArr}
          ],
          xAxis: [
            {
              type: 'category',
              data: categoryArr,
              boundaryGap: false,
              axisLine: {onZero: false},
              splitLine: {show: false},
              min: 'dataMin',
              max: 'dataMax',
              axisPointer: {
                z: 100
              }
            }
          ]
        }
        console.log(valueArr)
        worthChart.setOption(option)
        worthChart.dispatchAction({
          type: 'brush',
          areas: [
            {
              brushType: 'lineX',
              // coordRange: ['2019-06-02', '2020-06-20'],
              coordRange: [this.startDate, this.endDate],
              xAxisIndex: 0
            }
          ]
        });
        worthChart.on("brushEnd", (params) => {
          console.log(params)
          if (params && params.areas && params.areas.length > 0) {
            const range = params.areas[0].coordRange

            this.startDate = categoryArr[range[0]]
            this.endDate = categoryArr[range[1]]
            console.log(this.startDate)
            console.log(this.endDate)
            console.log(categoryArr[range[0]])
            console.log(categoryArr[range[1]])

            const startValue = volumes[range[0]]
            const endValue = volumes[range[1]]
           // console.log((endValue - startValue) / startValue * 100)
            let add = (endValue - startValue) / startValue * 100
            this.addUp = parseFloat(add+'').toFixed(2)
          }
        })
      }
    },
    initWorthChart() {
      worthChart = this.$echarts.init(this.$refs.worthChart, 'light');
      let option = {

        tooltip: {
          trigger: 'axis',
          position: function (pt) {
            return [pt[0], '10%'];
          }
        },
        title: {
          left: 'center',
          text: ''
        },
        /* toolbox: {
             feature: {
                 dataZoom: {
                     yAxisIndex: 'none'
                 },
                 restore: {},
                 saveAsImage: {}
             }
         },*/
        xAxis: {
          type: 'time',
          boundaryGap: false
        },
        yAxis: {
          type: 'value',
          boundaryGap: [0, '100%']
        },
        dataZoom: [
          {
            type: 'inside',
            start: 0,
            end: 100
          },
          {
            start: 0,
            end: 100
          }
        ],
        series: [
          {
            lineStyle: { // 设置线条的style等
              normal: {
                color: '#ea4829', // 折线线条颜色:红色
              },
            },
            itemStyle: {
              normal: {
                lineStyle: {
                  width: 1, //折线宽度
                },
                color: "red"
              }
            },
            name: '单位净值',
            type: 'line',
            //smooth: true,
            symbol: 'none',
            areaStyle: {
              color: {
                colorStops: [  // 渐变颜色
                  {
                    offset: 0,
                    color: '#fcf3f0',
                  },
                ],
                global: false,
              }
            },
            data: []
          }
        ],
        brush: {
          xAxisIndex: 'all',
          brushLink: 'all',
          outOfBrush: {
            colorAlpha: 0.1
          }
        },
        legend: {},
        visualMap: {
          pieces: [
            {
              gt: 0,
              lte: 50,
              color: '#93CE07'
            },
            {
              gt: 50,
              lte: 100,
              color: '#FBDB0F'
            }
          ],
          outOfRange: {
            color: '#999'
          }
        },
        toolbox: {
          feature: {
            dataZoom: {
              yAxisIndex: false
            },
            brush: {
              type: ['lineX', 'clear']
            }
          }
        },
      }
      worthChart.setOption(option, true)


    }
  }


}
</script>

